.photo__heading-title-catalog {
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-transform: uppercase;
  color: #000000;
  text-align: center;

  @media (min-width: $tablet-width) {
    font-size: 30px;
    margin-top: 66px;
  }

  @media (min-width: $desktop-width) {
    margin-top: 100px;

  }
}

.photo__heading-description-catalog {
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  text-transform: uppercase;
  color: #333333;
  text-align: center;
}

.photo-container {
  padding: 0 20px;

  @media (min-width: $tablet-width) {
    padding: 0;
  }
}

.photo__wrapper {
  margin-top: 50px;

  @media (min-width: $tablet-width) {
    margin-top: 66px;
  }

  @media (min-width: $desktop-width) {
    margin-top: 67px;
  }
}

.photo__list {
  padding: 0;
  min-height: 200px;
  list-style: none;

  @media (min-width: $tablet-width) {
    width: 768px;
    display: grid;
    grid-template-columns: 324px 324px;
    padding: 0 60px 0 60px;
  }

  @media (min-width: $desktop-width) {
    width: 1200px;
    display: grid;
    grid-template-columns:  400px 400px 400px;
    padding: 0;
  }
}

.photo-fest {
  @media (min-width: $tablet-width) {
    grid-column: 1 / 3;
  }

  @media (min-width: $desktop-width) {
    grid-column: 1 / 4;
    margin-bottom: -7px;
  }
}

.photo__link{
  text-decoration: none;
}

.photo__name {
  font-weight: 700;
  font-size: 14px;
  line-height: 21px;
  text-transform: uppercase;
  color: #000000;


  @media (min-width: $tablet-width) {
    margin-bottom: 0;
  }

  @media (min-width: $desktop-width) {
    color: #FFFFFF;
  }

}

.photo__title {
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  color: #333333;

  @media (min-width: $tablet-width) {
    margin-top: 0;
  }

  @media (min-width: $desktop-width) {
    color: #FFFFFF;
  }
}

.photo__description {
  font-weight: 700;
  font-size: 14px;
  line-height: 21px;
  color: #000000;
  margin-top: 2px;
  margin-left: 10px;

  @media (min-width: $desktop-width) {
    color: #FFFFFF;
  }
}

.photo__photo-title-catalog {
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  text-transform: uppercase;
  color: #000000;
  text-align: center;

  @media (min-width: $tablet-width) {
    margin-top: 58px;
    font-size: 30px;
  }

  @media (min-width: $desktop-width) {
    margin-top: 50px;
  }
}

.photo__photo-description-catalog{
  position: relative;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  text-transform: uppercase;
  color: #333333;
  text-align: center;
  margin: 38px 5px;
}

.photo__video {
  display: block;
  box-sizing: border-box;
  text-align: center;
  margin: auto;
}

.photo-like-wrapper {
  display: flex;
  margin-right: 20px;

  @media (min-width: $tablet-width) {
    margin-top: 30px;
  }


}

.photo-white-lake {
  @media (min-width: $desktop-width) {
    background-image: url("../../img/like-icon-white.svg");
    content: "";
    background-repeat: no-repeat;
    background-position: center;
  }
}

.photo__photo-description-catalog::after {
    content: "";
    position: absolute;
    background-repeat: no-repeat;
    background-position: center;
    bottom: -166px;
    left: 109px;
    width: 48px;
    height: 54px;
    background-image: url("../img/play.svg");

    @media (min-width: $tablet-width) {
      left: 358px;
      bottom: -326px;
    }

    @media (min-width: $desktop-width) {
      left: 580px;
    }

}

.photo__tablet-br {
  display: none;



  @media (min-width: $tablet-width) {
    display: inline;
  }
}

.photo-info-wrapper {
  @media (min-width: $tablet-width) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    background-color: #F2F2F2;
    margin-top: -6px;
    min-height: 80px;
  }

  @media (min-width: $desktop-width) {
    position: absolute;
    background-color: rgba(0, 0, 0, 0.3);
    margin-top: -86px;
    z-index: 1;
    width: 400px;
  }
}

.photo-info-wrapper2 {
  @media (min-width: $tablet-width) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    background-color: #EAEAEA;
    margin-top: -6px;
    min-height: 80px;
  }

  @media (min-width: $desktop-width) {
    position: absolute;
    background-color: rgba(0, 0, 0, 0.3);
    margin-top: -86px;
    z-index: 1;
    width: 400px;
  }
}

.photo-info-title {
  @media (min-width: $tablet-width) {
    margin-left: 20px;
  }

}

.photo__videow-wrapper {
  @media (min-width: $tablet-width) {
    padding: 0 20px;
  }
}

.photo-info-wrapper-width1 {
  @media (min-width: $desktop-width) {
    width: 1200px;
  }
}


